<template>
        <section class="faq-section">
            <div class="coll-full">
               <h2 class="title1">Preguntas Frecuentes</h2>
                <div class="faq-container">
                    <!-- Loop through faqs array and display questions -->
                    <div v-for="(faq, index) in faqs" :key="index" class="faq-item">
                        <button class="faq-question" @click="toggleAnswer(index)">
                            {{ faq.question }}
                        </button>
                        <div class="faq-answer" v-show="faq.isOpen">
                            <p>{{ faq.answer }}</p>
                        </div>
                    </div>
                </div>
            </div>
        </section>   
</template>

<script>
import { reactive } from 'vue'
export default {
    name: "ContactInfo",
    
     setup() {
    // FAQ data with isOpen property to toggle answers visibility
    const faqs = reactive([
      {
        question: "¿A qué hora debo llegar a la ceremonia?",
        answer: `Para quienes asistan a la ceremonia, por favor lleguen a las 4pm. 
    Para quienes solo asistan a la fiesta, por favor lleguen a las 6pm.
    Mensaje especial para los invitados mexicanos: ¡No lleguen tarde!`,
        isOpen: false
      },
      {
        question: "Código de vestimenta",
        answer: `Aunque la boda será frente al mar, no tendrás que caminar sobre la arena. Los colores vivos lucen muy bien en la playa, pero esperamos que tu atuendo sea formal. Evita ropa muy casual como jeans, shorts o sandalias. Queremos verlos guapos ;)`,
        isOpen: false
      },
      {
        question: "¿Puedo llevar a mis hijos?",
        answer: `Es una boda solo para adultos, por lo que no habrá niños. Si vienes de fuera con tus hijos, ¡mándanos un mensaje!`,
        isOpen: false
      },
      {
        question: "Regalos",
        answer: `Como tenemos que regresar a Ámsterdam, los regalos grandes o físicos no son muy prácticos.
Para quienes quieran hacernos un regalo, hemos creado un fondo para nuestra luna de miel. ¡Contribuir es completamente opcional!
Su presencia ya es el mejor regalo y estamos muy agradecidos por el esfuerzo que han hecho para acompañarnos en este día tan especial.
Pueden encontrar todos los detalles en la sección Mesa de Regalos de esta página.`,
        isOpen: false
      },
      {
        question: "¿Puedo tomar fotos en su boda? ¿Cuál es su hashtag?",
        answer: `Durante la ceremonia guarda el celular, relájate y disfruta el momento con nosotros.
Después, ya en la fiesta, ¡saca tu teléfono y toma todas las fotos y videos que quieras!
Nuestro hashtag es #grecia&gunnar`,
        isOpen: false
      },
    ]);

    // Function to toggle visibility of the answer
    const toggleAnswer = (index) => {
      faqs[index].isOpen = !faqs[index].isOpen;
    };

    // Expose data and methods to the template
    return { faqs, toggleAnswer };
  }

};
</script>
  
